.write {
    padding-top: 50px;
}

.writeImg {
    margin-left: 150px;
    width: 70vw;
    height: 250px;
    border-radius: 10px;
    object-fit: cover;
}

.writeForm {
    position: relative;
}

.writeFormGroup {
    margin-left: 150px;
    display: flex;
    align-items: center;
}

.writeIcon {
    width: 25px;
    height: 25px;
    border: 1px solid;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: grey;
    cursor: pointer;
}

.writeInput {
    font-size: 25px;
    font-weight: bold;
    border: none;
    padding: 20px;
    width: 70vw;
    font-family: 'Lora', serif;
}

.writeInput:focus {
    outline: none;
}

.writeText {
    font-size: 20px;
    height: 100vh;
    font-family: 'Josefin Sans', sans-serif;
}

.writeSubmit {
    position: absolute;
    top: 20px;
    right: 215px;
    color: white;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
}

.writeSubmit:hover {
    box-shadow: 0 0 5px black;
}